/* poppins-200 - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 200;
  font-display: swap;
  src: url('../assets/fonts/poppins-v20-latin-200.woff2') format('woff2'),
    url('../assets/fonts/poppins-v20-latin-200.woff') format('woff');
}
